<template>
  <div class="views" id="views">
    <div id="container" class="container"></div>
  </div>
</template>

<script>
import { App } from '@/assets/App/App'

const app = new App()
export default {
  data () {
    return {
      params: {
        methods: {
          animation: true, // 循环调用渲染
          renderer: true, // 应用渲染器
          light: true, // 在场景中加入全景光
          controls: true, // 使用轨道控制器，鼠标滚轮控制放大缩小，左键拖动绕中心点旋转，右键拖动中心点。
          statement: true // 在控制台打印声明，与本案例无关，可忽略。
        },
        events: {
          windowResize: true // 监听窗口尺寸变化，并改变渲染页面尺寸
        },
        params: {
          container: null, // 渲染结果输出dom页面
          renderer: [
            'WebGLRenderer'
          ],
          controls: [
            'OrbitControls'
          ],
          rendererParams: {
            antialias: true // 抗锯齿，可忽略。
          }
        }
      }
    }
  },
  mounted () {
    const that = this // 将this赋值给that
    this.params.params.container = document.getElementById('container') // 获取渲染结果输出dom页面
    /**
     * 初始化场景
     */
    app.init(this.params, (scene, camera, renderer) => {
      camera.position.set(0, 0, -600) // 修改相机位置
    })
    /**
     * 导入模型
     */
    app.addModel({
      url: 'static/model/fbx/cqcet/15/实训楼.FBX', // 模型地址
      type: 'fbx', // 模型格式
      name: 'tower' // 模型名
    }, () => { // 加载完成回调，可忽略。
      console.log('模型加载完成')
    })
    /**
     * 添加模型点击事件监听
     */
    app.addOn(app.scene, 'click',
      (objList) => {
        console.log(`点击了${objList[0].object.name}，其父模型是${objList[0].object.parent.name}`)
      },
      'test_click', {
        recursive: true
      },
      () => {
        console.log('没有点击到模型')
      })
    /**
     * 添加模型双击事件监听
     */
    app.addOn(app.scene, 'dblclick',
      (objList) => {
        console.log(`双击了${objList[0].object.name}，其父模型是${objList[0].object.parent.name}`)
      },
      'test_dblclick', {
        recursive: true
      },
      () => {
        console.log('没有双击到模型')
      })
    /**
     * 添加模型鼠标移动事件监听
     */
    app.addOn(app.scene, 'mousemove',
      (objList) => {
        if (that.in) return
        console.log(`鼠标移入了模型${objList[0].object.parent.parent.name}`)
        that.in = true
      },
      'test_mousemove', {
        recursive: true
      },
      () => {
        if (!that.in) return
        console.log('鼠标移出了模型')
        this.in = false
      })
  }
}
</script>

<style scoped>
.views {
  height: 100%;
  width: 100%;
}

.container {
  height: 100%;
  width: 100%;
}
</style>
